.App--container{
    display: flex;
    align-items: center;
    flex-direction: column;
    height: 100vh;
    width: 100%;
    background-attachment: fixed;
    background-image: url(./img/fundo01.PNG);
}

.register--container{
    padding: 20px 60px;
    border: 1px solid #fff;
    margin-top: 30px;
    border-radius: 20px;
    background-image: linear-gradient(45deg, #00BFFF 0%, 	#00FFFF 46%, #1E90FF 100%);
}

.register--title{
    text-align: center;
    margin-bottom: 20px;
    font-weight: bold;
    font-family: Arial, Helvetica, sans-serif;
}

.register--input{
    display: block;
    padding: 10px auto;
    margin: 10px auto;
    width: 330px;
    height: 35px;
    border-radius: 8px;
    border: 1px solid #fff;
    outline: none;
}

.bnt{
    position: relative;
    padding: 8px 12px;
    cursor: pointer;
    font-size: 15px;
    border-radius: 10px;
    left: 230px;
    background-image: linear-gradient(45deg, #1C1C1C 0%, 	#000000 46%, 	#363636 100%);
    color: #fff;
    font-weight: bold;
}

.bnt:hover{
    filter: drop-shadow(0 0 0.5rem #FFD700);
    background: transparent;
    color: #000000;
    font-weight: bold;
}